const Info = () => {
  const [showing, setShowing] = useState(false);

  const modal = useMemo(() => {
    if (!showing) {
      return null;
    }
    return (
      <Modal title="Oversikt" onClose={() => setShowing(false)}>
        <p>
          Oversikten er et verkt√∏y for √• hjelp du finne alle ordene i
          puslespillet. Radene er ord som starter med hver bokstav, og kolonnene
          er hvor mange ord av hver lengde.
        </p>
        <p>Oversikten viser ord totalt, ogs√•. Lykke til med √• finne alt!</p>
      </Modal>
    );
  }, [showing, setShowing]);

  return (
    <>
      <button className={classes.button} onClick={() => setShowing(true)}>
        <HelpIcon />
      </button>
      {modal}
    </>
  );
};

export default Info;
